}
.top-search .search-wrap {
  width: 100%;
  height: 30px;
  background: #fff;
  border-radius: 4px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: justify;
      justify-content: space-between;
  color: #c0c0c0;
  padding: 0 12px;
}
.top-search .search-wrap .placeholder {
  -ms-flex: 1;
      flex: 1;
  text-align: left;
  padding-left: 12px;
}
.top-search .search-wrap img {
  width: 20px;
  height: 20px;
}
.hot-wrap,
.recommend-wrap {
  padding-top: 12px;
}



