
.m-cell-media-wrap {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  padding: 18px 20px;
  position: relative;
}
.m-cell-media-wrap:after {
  content: '';
  position: absolute;
  left: 0;
  bottom: 0;
  right: 0;
  height: 1px;
  background: #eee;
  transform: scaleY(0.5);
}
.m-cell-media-wrap .m-cell-media-top {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
}
.m-cell-media-wrap .m-cell-media-top .m-cell-media {
  -ms-flex: 1;
      flex: 1;
  padding-right: 45px;
}
.m-cell-media-wrap .m-cell-media-top .m-cell-title {
  font-size: 17px;
  line-height: 22px;
  color: #333;
  font-weight: bold;
}
.m-cell-media-wrap .m-cell-media-top .m-cell-detail {
  font-size: 12px;
  padding-top: 12px;
  color: #939393;
}
.m-cell-media-wrap .m-cell-media-top .m-pull-right {
  width: 94px;
  height: 94px;
  overflow: hidden;
  background-position: center center;
  background-size: cover;
}
.m-cell-media-wrap .m-cell-media-top .m-pull-right img {
  width: 100%;
}
.m-cell-media-wrap .m-cell-media-bottom {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  padding-top: 20px;
  margin-top: 12px;
  color: #bfbfbf;
  position: relative;
}
.m-cell-media-wrap .m-cell-media-bottom:before {
  content: '';
  position: absolute;
  top: 0;
  left: 0;
  width: 20px;
  height: 1px;
  background: #eee;
}



