
.m-cell {
  position: relative;
  padding: 10px 5px 10px 15px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
}
.m-cell:before {
  content: '';
  position: absolute;
  left: 0;
  bottom: 0;
  right: 0;
  height: 1px;
  background: #eee;
  transform: scaleY(0.5);
}
.m-cell .m-cell-title {
  font-size: 15px;
}
.m-cell .m-cell-title img {
  width: 20px;
  height: 20px;
}
.m-cell .m-cell-right {
  font-size: 12px;
}
.m-cell .m-cell-right a {
  color: #666;
}
.m-cell .m-cell-right img {
  width: 20px;
  height: 20px;
}
.m-cell.hot {
  padding: 0px 5px 0px 15px;
  height: 22px;
}
.m-cell.hot:after {
  content: '';
  position: absolute;
  width: 5px;
  left: 0;
  top: 0px;
  bottom: 0px;
  background: #ff8447;
}
.m-cell.hot:before {
  height: 0;
}
.m-cell.recommend {
  padding: 0px 5px 0px 15px;
  height: 22px;
}
.m-cell.recommend:after {
  content: '';
  position: absolute;
  width: 5px;
  left: 0;
  top: 0px;
  bottom: 0px;
  background: #42bd56;
}
.m-cell.recommend:before {
  height: 0;
}



