/*导入颜色变量*/
.m-header {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  height: 44px;
  padding: 0 10px;
  background: #fff;
  color: #494949;
  position: relative;
}
.m-header:after {
  content: '';
  position: absolute;
  left: 0;
  bottom: 0;
  right: 0;
  height: 1px;
  background: #e4e4e4;
  transform: scaleY(0.5);
}
.m-header a {
  color: #494949;
}
.m-header .m-header-button {
  width: 70px;
  -ms-flex-align: stretch;
      align-items: stretch;
}
.m-header .m-header-button.is-left {
  text-align: left;
}
.m-header .m-header-button.is-right {
  text-align: right;
}
.m-header .m-header-button .m-icon-img {
  width: 20px;
  height: 20px;
}
.m-header .m-header-button .margin-right-10 {
  margin-right: 10px;
}
.m-header .m-header-title {
  -ms-flex: 1;
      flex: 1;
  text-align: center;
  font-size: 16px;
  text-overflow: ellipsis;
  white-space: nowrap;
  overflow: hidden;
}
.m-header.is-bg {
  background: #42bd56;
  color: #fff;
}
.m-header.is-bg a {
  color: #fff;
}
.m-header.is-bg .m-header-title {
  color: #fff;
}
.m-header.is-bg:after {
  content: '';
  position: absolute;
  left: 0;
  bottom: 0;
  right: 0;
  height: 0px;
  background: #e4e4e4;
  transform: scaleY(0.5);
}
.m-header.is-fixed {
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  z-index: 9;
}



